footer.footer {
  border-top: solid 1px $light-blue;
  padding: 40px 0;
  p {
    font-size: 16px;
    color: $footer-text;
    line-height: 21px;
    @include r($xs) {
      font-size: 12px;
      line-height: 16px; } }
  a {
    display: block;
    @include r($md) {
      margin-top: 20px; } }
  img {
    width: 122px; } }
