.page-transition {
  position: fixed;
  z-index: 1000000;
  width: 100%;
  height: 100%;
  background: #282d57;
  transition: all 0.7s cubic-bezier(0.53, -0.03, 0.66, 1.01);
  transform: translateX(100%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-transition.left {
  transform: translateX(100%);
}

.page-transition.right {
  transform: translateX(0%);
}

.page-transition .logo-white h1 {
  font-size: 25px;
  font-family: 'Segoe Ui';
  color: #ffffff;
  font-weight: 600;
  display: inline-block;
  margin-top: -10px;
}

.page-transition .logo-white h1 span {
  font-size: 7px;
  letter-spacing: 1.3px;
  text-align: right;
  font-weight: normal;
  display: block !important;
}

.barba-leave-active, .barba-enter-active {
  transition: opacity 1s ease;
}

/* initial state */
.barba-leave {
  opacity: 1;
}

.barba-enter, .barba-leave-to {
  opacity: 0;
}

.barba-enter-to {
  opacity: 1;
}

.header__mobile {
  position: fixed;
  width: 100%;
  height: 0;
  background: #ffffff;
  padding: 0;
  transition: all 0.6s cubic-bezier(0.74, 0.01, 0.29, 1);
  z-index: -1;
}

.header__mobile.active {
  height: 100%;
  padding: 35px 40px;
  z-index: 1000;
}

.header__mobile.active .block {
  opacity: 1;
}

.header__mobile .block {
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.74, 0.01, 0.29, 1);
}

.header__mobile h3 {
  font-size: 16px;
  letter-spacing: 1px;
  color: #ced9ef;
  font-weight: normal;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.header__mobile ul {
  margin-bottom: 35px;
}

.header__mobile ul li {
  line-height: 3;
}

.header__mobile ul li a {
  color: #45464b;
  height: 100%;
  display: flex;
  align-items: center;
  font-size: 21px;
  letter-spacing: 1.31px;
  text-decoration: none;
  text-transform: uppercase;
}

.header__mobile .header__info .phone {
  font-size: 20px;
}

.header__mobile .header__info .mail {
  font-size: 15px;
}

.menu-button {
  display: flex;
  justify-content: flex-end;
}

.menu-button span {
  display: block;
}

.menu-button span:before, .menu-button span:after {
  content: '';
  display: block;
  width: 40px;
  height: 2px;
  background: #282d57;
  border-radius: 1.5px;
  transition: .3s ease;
}

.menu-button span:after {
  margin-top: 10px;
}

.menu-button span.active:before {
  transform: rotate(45deg) translateY(7px);
}

.menu-button span.active:after {
  transform: rotate(-45deg) translateY(-9px) translateX(1px);
}
