.branchs {
  padding: 75px 0 20px;
  overflow-x: hidden;
  @include r($xs) {
    padding: 40px 0; }
  &__small {
    text-decoration: none;
    article {
      border-radius: 6px;
      height: 460px;
      box-shadow: 0 28px 60px 0 rgba(191, 204, 226, 0.24);
      margin-bottom: 20px;
      border-radius: 6px;
      overflow: hidden;
      @include r($md) {
        height: 500px; }
      @include r($xs) {
        height: initial; } }
    header {
      width: 100%;
      height: 245px;
      @include r($xs) {
        height: 220px; }
      img {
        width: 100%;
        height: 100%;
        @include object-fit(cover); } }
    main {
      padding: 40px 50px;
      @include r($xs) {
        padding: 30px; }
      h1 {
        font-size: 24px;
        line-height: 29px;
        font-weight: normal;
        letter-spacing: 1.5px;
        text-transform: uppercase;
        color: $dark-blue;
        margin-bottom: 12px;
        @include r($xs) {
          font-size: 20px;
          margin-bottom: 18px;
          line-height: 26px;
          letter-spacing: 1.25px; } }
      p {
        font-size: 14px;
        color: $dark-blue;
        letter-spacing: 0.94px;
        line-height: 21px;
        @include r($xs) {
          font-size: 14px;
          line-height: 20px;
          letter-spacing: 0.08px; } } } }
  &__big {
    text-decoration: none;
    &:hover {
      main {
        &:before {
          width: 200%; }
        div {
          background: $dark-blue;
          color: $white;
          right: -330px; } } }
    article {
      height: 460px;
      margin-bottom: 20px;
      position: relative;
      border-radius: 6px;
      overflow: hidden;
      @include r($md) {
        box-shadow: 0 28px 60px 0 rgba(191, 204, 226, 0.24);
        height: 500px; }
      @include r($xs) {
        height: initial; } }

    header {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @include r($md) {
        height: 245px;
        position: relative; }
      img {
        width: 100%;
        height: 100%;
        @include object-fit(cover); } }
    main {
      width: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 60px;
      color: $white;
      position: relative;
      transition: all .6s cubic-bezier(0.74, 0.01, 0.29, 1);
      &:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: $dodger-blue-70;
        transition: all .6s cubic-bezier(0.74, 0.01, 0.29, 1);
        @include r($xs) {
          display: none; } }
      @include r($md) {
        background: none;
        color: $dark-blue;
        padding: 40px 50px;
        width: 100%;
        height: initial; }
      @include r($xs) {
        padding: 30px; }
      h1 {
        font-size: 24px;
        letter-spacing: 1.5px;
        text-transform: uppercase;
        font-weight: normal;
        margin-bottom: 35px;
        line-height: 32px;
        position: relative;
        @include r($md) {
          margin-bottom: 12px; }
        @include r($xs) {
          font-size: 20px;
          margin-bottom: 18px;
          line-height: 26px;
          letter-spacing: 1.25px; } }
      p {
        font-size: 18px;
        letter-spacing: 1.13px;
        line-height: 25px;
        position: relative;
        @include r($md) {
          font-size: 15px; }
        @include r($xs) {
          font-size: 14px;
          line-height: 20px;
          letter-spacing: 0.08px; } }
      div {
        background-color: $white;
        color: $dark-blue;
        font-size: 13px;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        text-decoration: none;
        position: absolute;
        right: -30px;
        bottom: 60px;
        transition: all .6s cubic-bezier(0.74, 0.01, 0.29, 1);
        @include r($md) {
          display: none; }
        i {
          transform: rotate(-180deg); } } } } }


