body {
  font-family: 'Helvetica';
  &.active {
    overflow: hidden;
    position: fixed; } }

.flex {
  display: flex; }

.h-100 {
  height: 100%; }

.container {
  max-width: 1200px;
  width: 100% !important;
  box-sizing: border-box;
  padding: 0 20px;
  @include r($xs) {
    padding: 0 40px; } }


@include r($xs) {
  .hidden-xs {
    display: none !important; } }
@include rmin(768) {
  .hidden-md {
    display: none !important; } }


.heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  h2 {
    text-align: center;
    text-transform: uppercase;
    font-size: 31px;
    letter-spacing: 1.94px;
    color: $dark-blue;
    margin-bottom: 15px;
    font-weight: normal;
    @include r($xs) {
      font-size: 25px;
      letter-spacing: 1.56; } }
  .line {
    width: 60px;
    height: 5px;
    background: $blue;
    border-radius: 2px;
    position: relative;
    z-index: 100;
    &__thin {
      height: 1px;
      background: $cloudy-blue;
      position: absolute;
      z-index: -10;
      width: 0px;
      transform: translateY(1.9px);
      left: 60px;
      transition: all 2s cubic-bezier(0.74, 0.01, 0.29, 1); } } }


.text {
  background: $grey;
  padding: 115px 0;
  @include r($xs) {
    padding: 60px 0; }
  p {
    color: $dark-blue;
    font-size: 18px;
    letter-spacing: 1.13px;
    line-height: 23px;
    &:first-child {
      margin-bottom: 26px; } } }

#map-mobile {
  width: 100%;
  height: 400px;
  display: none;
  @include r($md) {
    display: block; } }


.logo {
  width: 100%;
  display: flex;
  text-decoration: none;
  h1 {
    font-size: 25px;
    font-family: 'Segoe Ui';
    color: $dark-blue;
    font-weight: 600;
    display: inline-block;
    span {
      font-size: 7px;
      letter-spacing: 1.3px;
      text-align: right;
      font-weight: normal;
      display: block; } } }
